#blog_post_details {
    font-family: $Poppins;

    .blog-post {
        .fetured-img {
            img {
                width: 100%;
                max-width: 750px;
                max-height: 460px;
            }
        }

        .post-date {
            margin: 2rem 0 .5rem;

            p {
                color: #000;
                margin: 0;
            }
        }

        .post-title {
            h1 {
                font-size: 28px;
                transition: .5s;

                &:hover {
                    color: $primary-color;
                }
            }
        }

        .post-details {
            margin-bottom: 1rem;

            span {
                margin: 0 0.8rem;
                color: #999;

                i {
                    margin-right: .5rem;
                }

                &:first-child {
                    margin-left: 0;
                }
            }
        }

        .post-content {}

        .st {
            padding: 2rem 0;
            border-top: 1px solid #ddd;

            .dd {
                display: flex;
                justify-content: space-between;

            }
        }

        .post-tags {

            display: flex;
            align-items: center;


            h4 {
                margin: 0;
                font-weight: bold;
            }

            a {
                color: #999;
                margin: .1rem;
                padding: .4rem .4rem;
                border: 1px solid #ddd;
                border-radius: 5px;
                transition: .5s;

                &:hover {

                    background: $primary-color;
                    color: #fff;
                }
            }
        }

        .post-share {
            display: flex;
            align-items: center;

            h4 {
                margin: 0;
                font-weight: bold;
            }

            a {
                color: #333;
                margin: .2rem;
                padding: .3rem;
                transition: .5s;

                &:hover {
                    color: $primary-color;
                }


            }
        }

        .post-communt {
            border-top: 1px solid #ddd;
            padding: 2rem 0;

            .comments {
                h3 {
                    font-weight: bold;
                    font-size: 32px;
                    color: #333;
                    margin-bottom: 2rem;
                }

                p {
                    font-size: 15px;
                    margin-bottom: .5rem;
                }

                .comment-form {
                    .form-group {
                        margin-bottom: 0;

                        .common-button {
                            padding: .6rem 2.5rem;

                            &:hover {
                                background: transparent;
                                color: $primary-color;
                            }
                        }

                    }

                    .form-control {
                        border-radius: 0;
                        margin: 1rem 0;
                        outline: none;
                        border-color: #ddd;

                        &:hover {
                            border-color: $primary-color;

                        }


                    }
                }

            }
        }
    }


}
