#about_event_section{
 .about_event_content{
   transition: .5s;
   &:hover{
     .icon-img{
       img{
transform: rotateY(180deg);
       }
     }
   }
   .icon-img{
     img{
       transition: .5s;
     }
   }

   h3{
     margin-top: 24px;
     font-weight: bold;
     font-size: 22px;
     font-family: $Poppins;
   }
   p{
     font-size: 15px;
     font-family: $Poppins;
   }
  }
}
#about{
  overflow: hidden;
  .row{
    .col-lg-3,.col-md-6{
      margin: 0 !important;
      padding: 0 !important;


        img{
          width: 100%;
          height: 100%;
        }

      .event-overview{
        color: #ffffff;
        padding:4rem 1.5rem;
        img{
          width: auto;
          margin: 1.5rem 0;
        }
        h2{
          color: #ffffff;
          font-weight: bold;

        }
        p{
          font-family: $Poppins;
        }

      }
    }
  }

}