// Your custom styles
@import url('https://fonts.googleapis.com/css?family=Courgette|Kalam:400,700|Lato:400,700|Lemonada:400,600,700|Open+Sans:400,600,700|Roboto+Condensed:400,700|Poppins:400,700&display=swap');

$open-sans: 'Open Sans',
sans-serif;
$lato: 'Lato',
sans-serif;
$robort-c : 'Roboto Condensed',
sans-serif;
$courgette: 'Courgette',
cursive;
$kalam: 'Kalam',
cursive;
$lemonada: 'Lemonada',
cursive;
$Poppins: 'Poppins',
sans-serif;

$primary-color:#FF2D55;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.navbar-toggler-icon {
	background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(255, 255, 255, 0.9)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")
}
html
ul {
	li {
		list-style: none;
	}
}

a {
	text-decoration: none;
	color: rgba(255, 255, 255, 0.85);
}

h1 {
	font-size: 40px;
}

h2 {
	font-size: 32px;
}

h3 {
	font-size: 28px;
}

h4 {
	font-size: 24px;
}

h5 {
	font-size: 20px;
}

h6 {
	font-size: 16px;
}

p,
span,
a {
	font-size: 16px;
}
.section {
	padding: 5rem 0;
}

.action-btn {
	background: $primary-color;
	border-radius: 2rem;
	color: #ffffff;
	text-transform: capitalize;
	font-size: 1.1rem;
	padding: .5rem 1.5rem;
}
.common-button{
	background: $primary-color;
	border-radius: 0;
	color: #ffffff;
	text-transform: capitalize;
	font-size: 1.1rem;
	padding: .5rem 1.5rem;
	box-shadow: none;
	margin:1rem	 0 0;
}
.form-control{
	margin: .5rem 0;

}
.form-control:focus{
	box-shadow: none;
	border-color: $primary-color;
}
a.buy {
	border-color: $primary-color;

	&:hover {
		background-color: transparent;
		border-color: #5e5e5e;
	}

}

a.download {
	background-color: transparent;

	&:hover {
		background-color: $primary-color;
		border-color: $primary-color;
	}
}

a.more {
	border-color: $primary-color;
	padding: 1rem 3rem;
	margin-top: 3rem;

	&:hover {
		background-color: transparent;
		border-color: #5e5e5e;
	}
}
.scrolled {
	background-color: rgba(28,28,28,0.88);
	transition:padding 1s ease-in-out;
	.navbar-brand {
		height: 70px;
	}
	ul#nav {
		li{
			a{
				padding: 30px 10px;
			}
			a.sub_menu_link{
				padding: 30px 20px 30px 10px;
			}
		}
	}

}

.section-content {
	margin-top: 2.5rem;
}
header {
	background-color: transparent;
	border-bottom: 1px solid #5b5b5b;

	nav {
		.navbar-brand {
			padding: 0;
			box-shadow: none;

			span {
				font-size: 3rem;
				text-transform: capitalize;
			}

			.first-brand {
				color: $primary-color;
				font-weight: bold;
				font-family: Roboto, sans-serif;
			}

			.last-brand {
				text-transform: uppercase;
				color: #ffffff;
			}

		}

		.navbar-nav {
			.nav-item {
				.nav-link {
					color: #ffffff;
					text-transform: capitalize;
					font-weight: 400;
					font-family: Roboto, sans-serif;
					line-height: 82px;
				}

				&:hover {
					.nav-link {
						color: $primary-color;
					}
				}

				&.active {
					position: relative;

					&::after {
						content: '';
						position: absolute;
						bottom: 0;
						left: 0;
						width: 100%;
						height: 5px;
						background: $primary-color;
					}
				}
			}

			.dropdown {
				.dropdown-menu {
					.dropdown-item {
						color: #000000;
						text-transform: capitalize;
						font-size: 1.1rem;
						font-weight: 400;
						font-family: Roboto, sans-serif;
						padding: .5rem;
						margin: 0;
						display: block;

						&:hover {
							background: $primary-color;
							color: #ffffff;
						}

					}
				}
			}

		}

		.action-btn {
			background: $primary-color;
			border-radius: 2rem;
			color: #ffffff;
			text-transform: capitalize;
			font-size: 1.1rem;
			padding: .5rem 1.5rem;
		}
	}
}

#banner{

	.banner-overlay{
		position: relative;
		.banner-img{
			img{
				height: 350px;
				width: 100%;
			}
		}
		.banner-content{
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(8, 8, 8, 0.9);
			.page-heading{
				position: relative;
				width: 100%;
				height: 100%;
				.heading-content{
					position: absolute;
					top: 60%;
					left: 50%;
					transform: translate(-50%,-50%);

					.page-title{
						text-transform: capitalize;
						font-weight: bold;
						color: #ffffff;
						font-size: 3rem;
					}
					.page-status{
						text-align: center;
						a{
							text-transform: capitalize;
							font-weight: bold;
							font-size: 1.2rem;
							&:hover{
								color: $primary-color;
							}
						}
						i{
							color: #ffffff;
							margin:0 8px ;
						}
						.page-active{
							color: $primary-color;
						}
					}
				}
			}
		}
	}
}

#home_banner {
	margin-top: -100px;
	height: 800px;
	background: linear-gradient(to right, rgba(17, 17, 17, 0.8), rgba(17, 17, 17, 0.8)), url("../img/meeting.jpg") no-repeat scroll center top;
	background-size: cover;

	.carousel-item {
		height: 800px;

		.carousel-caption {
			text-align: left;
			top: 40%;

			h3.slider-subtitle {
				font-family: Roboto, sans-serif;
				font-size: 50px;
				text-transform: uppercase;
				font-weight: 300;
				color: $primary-color;

			}

			h2.slider-title {
				font-family: $Poppins;
				font-size: 50px;
				text-transform: uppercase;
				font-weight: bold;
			}

			p.slider-text {
				font-family: $Poppins;
				font-size: 1.1rem;
			}

			a {
				border: 1px solid #5e5e5e;
				padding: .8rem 2.5rem;
				color: #ffffff;
			}

		}
	}
}


.heading {
	text-align: center;

	h3.subtitle {
		font-family: Roboto, sans-serif;
		font-size: 24px;
		text-transform: capitalize;
		font-weight: 500;
		color: $primary-color;
		font-style: italic;

	}

	h2.title {
		font-family: $Poppins;
		font-size: 36px;
		text-transform: uppercase;
		font-weight: bold;
		line-height: 60px;
	}

	p.text {
		font-size: 15px;
		font-family: $Poppins;
		max-width: 700px;
		margin: 1rem auto;
		display: block;
	}
}

#about_section {
	.heading {
		text-align: left;
	}

	.section-content {
		margin-top: 2.5rem;

		a.action-btn {
			border: 1px solid rgba(138, 138, 138, 0.59);
			padding: .8rem 2.5rem;
			color: #ffffff;
		}

		a.buy {
			border-color: $primary-color;

			&:hover {
				background-color: transparent;
				border-color: rgba(138, 138, 138, 0.59);
				color: #000000;
			}

		}

		a.download {
			background-color: transparent;
			color: #000000;

			&:hover {
				background-color: $primary-color;
				border-color: $primary-color;
				color: #ffffff;
			}
		}

		p {
			margin-bottom: 2rem;
			font-family: $Poppins;
			color: #545454;
		}

		.video {
			position: relative;

			img {
				width: 100%;
			}

			.play {
				position: absolute;
				top: 50%;
				left: 50%;
				background: #3947ff;
				text-align: center;
				margin: -40px 0 0 -40px;
				border-radius: 80px;
				width: 80px;
				height: 80px;
				color: #ffffff;
				font-size: 22px;
				vertical-align: center;
				padding-top: 23px;
			}
		}

	}
}

#event_section {
	background: linear-gradient(to right, rgba(17, 17, 17, 0.8), rgba(17, 17, 17, 0.8)), url("../img/event_section-coundown.jpg") no-repeat fixed center center;
	background-size: cover;


	h2.title {
		color: #ffffff;
	}

	p.text {
		color: #ffffff;
	}

	.section-content {
		color: #ffffff;
	}
}


#schedule_section {
	.section-content {
		.conference-schedule {
			box-shadow: 0 0 10px rgba(51, 51, 51, 0.64);
			margin-top: 3rem;

			.schedule-date {
				text-align: center;
				padding: 2rem;
				background: #0D80F6;

				h3 {
					font-weight: 500;
					color: #dddddd;
				}
			}

			.schedule {
				padding-bottom: 30px;
				.schedule-list {
					display: flex;
					align-items: center;
					padding: .5rem 0;
					border-bottom: 1px solid rgba(147, 147, 147, 0.49);

					div {
						padding: .5rem;
					}

					.lecturer-img {
						img {
							width: 100%;
							height: 100%;
						}
					}

					.lecture-details {
						display: flex;
						justify-content: space-between;
						align-items: center;
						width: 100%;

						h5 {
							font-weight: 500;
							font-size: 18px;
						}

						p {
							margin: 0;

							a {
								color: $primary-color;
							}
						}

						.lecture-time {
							p {
								margin: 0;
								font-weight: bold;
								font-family: $Poppins;
								font-size: 15px;
							}
						}
					}

					.h2-lecture-details{
						padding: 0;
						h5 {
							font-weight: 500;
							font-size: 18px;
							margin-bottom: 0;
						}

						p {
							margin: 0;
							font-size: 15px;

							a {
								color: $primary-color;
							}
						}

						.lecture-time {
							padding: 0;
							p {
								margin: 0;
								padding: 0;
								font-weight: bold;
								font-family: $Poppins;
								font-size: 15px;
							}
						}
					}

				}
			}
		}
	}
}

#venue_section {
	background: linear-gradient(to right, rgba(17, 17, 17, 0.8), rgba(17, 17, 17, 0.8)), url("../img/event_section-coundown.jpg") no-repeat fixed center center;
	background-size: cover;

	.heading {
		.title {
			color: #ffffff;
		}

		.text {
			color: #ffffff;
		}
	}

	.venues {
		.venue {

			&:hover {
				background: #fff;

				.venue-details {
					h3 {

						color: $primary-color;
					}

					p {
						color: #000000;
					}
				}
			}

			.venue-img {
				position: relative;

				img {
					width: 100%;
				}

				.venue-place {
					position: absolute;
					top: 20px;
					right: 5%;
					background: #6027ff;
					padding: .5rem 1rem;
					border-radius: 2rem;
					color: #fff;
					font-family: $Poppins;
				}
			}

			.venue-details {
				padding: 1rem;
				border: 1px solid #999999;
				border-top: none;


				h3 {
					font-size: 18px;
					font-family: $Poppins;
					color: #ffffff;
					margin: 1rem 0;

				}

				p {
					font-size: 15px;
					font-family: $Poppins;
					color: #ffffff;
				}
			}
		}
	}

	.owl-dots {
		display: none;
	}

	.owl-nav {
		position: relative;

		.owl-prev {
			text-align: center;
			width: 50px;
			height: 50px;

			background: $primary-color;
			border-radius: 40px;
			position: absolute;
			top: -250px;
			left: -100px;

			span {
				font-size: 2rem;
				color: #ffffff;
				line-height: 28px;

			}
		}

		.owl-next {
			text-align: center;
			width: 50px;
			height: 50px;

			background: $primary-color;
			border-radius: 40px;
			position: absolute;
			top: -250px;
			right: -100px;

			span {
				font-size: 2rem;
				color: #ffffff;
				line-height: 28px;
			}
		}

	}
}

#uc_event_section {

	.up-coming-event {
		position: relative;
		overflow: hidden;
		transition: 1s;

		&:hover .event-text {
			height: 100%;
			opacity: 1;
			bottom: 0;
			cursor: pointer;
		}

		.event-text {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;

			width: 100%;
			text-align: center;
			background: rgba(0, 0, 0, 0.9);
			height: 0;
			transition: .5s;
			opacity: 0;

			.name {
				margin-top: 25%;
				color: #ffffff;
				text-transform: capitalize;
				font-size: 20px;
				margin-bottom: 0;

				transition: 1.5s;
			}

			a {
				color: $primary-color;
				text-transform: capitalize;
				font-size: 20px;


			}
		}
	}
}

#clients_section {
	background: linear-gradient(to right, rgba(17, 17, 17, 0.8), rgba(17, 17, 17, 0.8)), url("../img/reviewbgf.jpg") no-repeat fixed center center;
	background-size: cover;

	.heading {
		h2.title {
			color: #ffffff;
		}

		p.text {
			color: #ffffff;
		}
	}

	.section-content {

		.testimonial {
			padding: 2rem 0;

			.client {
				.client-review {
					i {
						font-size: 50px;
						padding-bottom: 30px;
					}

					p {
						color: #ffffff;
						font-family: $Poppins;
					}

					margin-bottom: 30px;
				}

				.client-img {
					display: flex;
					flex-direction: row;
					align-items: center;

					img {
						margin-right: 1rem;
						width: 60px;
						height: 60px;
					}

					.client-details {
						a.client-name {
							color: #ffffff;

							h4 {
								font-weight: bold;
								font-size: 1.2rem;
							}

							&:hover {
								color: $primary-color;
							}
						}

						.desig {
							color: #ffffff;
							font-size: 15px;
						}
					}
				}
			}
		}
	}
}

#speaker_section {
	.speakers {
		.speaker {
			margin-top: 3rem;

			.s-img {
				position: relative;
				overflow: hidden;

				img {
					width: 100%;
				}

				.s-social-link {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;

					transition: .5s;

					.social-links {
						position: absolute;
						bottom: -40px;
						left: 50%;
						width: 100%;
						transform: translateX(-50%);
						text-align: center;
						transition: .5s;

						a {
							width: 40px;
							height: 40px;
							text-align: center;
							display: inline-block;
							margin: 0 3px;
							font-size: 20px;
							line-height: 40px;

							i {}
						}
					}
				}
			}

			.s-details {
				text-align: center;
				margin-top: 1rem;

				a {
					h5 {
						color: #000000;
						font-weight: bold;
						transition: .3s;
					}

					&:hover h5 {
						color: $primary-color;
					}
				}

				p {
					color: #000000;
				}
			}

			&:hover {
				.s-social-link {
					background: rgba(40, 40, 40, 0.79);

					.social-links {
						bottom: 20px;
					}

				}
			}
		}
	}

}

#sponsor_section {
	background: #F4F4F4;

	.sponsor {
		.gold-sponsor {
			margin-top: 3rem;

			h3 {
				font-weight: bold;
			}

			img {
				margin: 2rem 1rem;
			}
		}

		.silver-sponsor {
			margin-top: 3rem;

			h3 {
				font-weight: bold;
			}

			img {
				margin: 2rem 1rem;
			}
		}
	}
}

#blog_section {
	.newses {
		display: block;
		box-shadow: 0 0 2px rgba(51, 51, 51, 0.49);
		margin-top: 50px;

		.news {
			.news-img {
				position: relative;
				overflow: hidden;

				img {
					width: 100%;
					transition: .5s;
				}

				p.news-publish {
					position: absolute;
					left: 4%;
					bottom: 0;
					background: #0110ee;
					padding: .5rem 1.5rem;
					border-radius: 2rem;
					font-family: $Poppins;
					transition: .5s;
				}
			}

			.news-details {
				padding: 1rem;

				h3 {
					color: #000000;
					font-weight: 500;
					font-size: 24px;
					font-family: $Poppins;
					transition: .5s;
				}

				.details {
					display: flex;

					p {
						color: #000000;
						margin-right: 5px;
					}
				}

				p.text {
					color: #555555;
					font-family: $Poppins;
				}

				p.mor {
					color: $primary-color;
				}
			}
		}

		&:hover {
			box-shadow: 0 0 15px rgba(136, 136, 136, 0.6);

			.news-details h3 {
				color: $primary-color;
			}

			img {
				transform: scale(1.2);
			}
		}
	}
}

footer {
	background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("../img/footer-bg.png") no-repeat center top scroll;
	background-size: cover;
	color: #ffffff;
	font-family: $Poppins;

	.widget {
		.widget-title {
			font-size: 1.5rem;
		}

		.widget-content {
			margin-top: 2rem;
		}
	}

	.sub-widget {
		.sub-widget-title {
			font-size: 1.2rem;
			margin-bottom: 2rem;
		}

		.sub-widget-content {}
	}

	.logo-brand {
		padding: 0;
		box-shadow: none;

		span {
			font-size: 3rem;
			text-transform: capitalize;
		}

		.first-brand {
			color: $primary-color;
			font-weight: bold;
			font-family: Roboto, sans-serif;
		}

		.last-brand {
			text-transform: uppercase;
			color: #ffffff;
		}

	}

	.useful-link {
		a {
			display: block;
			font-size: 17px;
			margin: .5rem 0;
		}
	}

	.instagram-img {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;

		a {
			display: inline-block;
		}
	}

	.f-social-link {
		a {
			width: 40px;
			height: 40px;
			text-align: center;
			display: inline-block;
			margin: 0 3px;
			font-size: 20px;
			line-height: 40px;
			border-radius: 30px;

			i {}
		}
	}

	.contact-details {
		.contact-info {
			display: flex;
			margin: .5rem 0;

			.icon {
				margin-right: 10px;
				color: $primary-color;
			}

			a {
				&:hover {
					color: $primary-color;
				}
			}
		}
	}

	.subscribe {
		margin-top: 2rem;

		input[type="email"] {}

		padding: .5rem;

		.input-group-append {
			.input-group-text {
				margin: 0;
				padding: 0;

				input[type="submit"] {
					border: none;
					background: $primary-color;
					padding: 5px 10px;
					border-radius: 5px;
					color: #ffffff;
				}
			}
		}


	}
}

footer.section {
	padding-bottom: 0;
}

.footer-bottom{
	padding: 1rem	0;
	background: rgba(0, 0, 0, 0.74);
	.footer-bottom-content{
		display: flex;
		align-items: center;
		justify-content: space-between;
		p{
			margin-bottom: 0;
		}
		.b-footer-menu{

		}
	}


}

.scroll-top{
	position: fixed;
	bottom: 4%;
	right:  4%;
	background: #fff;
	width: 60px;
	height: 60px;
	border-radius: 30px;
	text-align: center;
	transition: .5s;

	i{
		line-height: 55px;
		font-size: 28px;
	}
	&:hover{
		cursor: pointer;
		color: #ffffff;
		background: $primary-color;
	}
}

.p-pagination{
	margin-top: 2rem;
	.pagination{
		.disabled{
			.page-link{
				color: #999999 !important;
			}
		}
		.p-active{
			background: $primary-color;
			.page-link{
				color: #ffffff !important;
			}
		}
		.page-item{
			.page-link{
				color: #000000;
				border: 1px solid #ddd;
				&:hover{
					background: $primary-color;
				}
			}
		}
	}
}


