

#gallery_section{
  background: #f4f4f4;
  .galleries{
    figure{
      &:first-child{
        padding-top: 0;
      }
    }
  }
}
.center{
  text-align: center;
}
#price_section{
  .price-table{
    .package{
      &:hover{
        .p-price{
          background: $primary-color;
          color: #fff;
          h2{
            color: #fff;

          }
        }
        a.buy-package{
          background: $primary-color;
          color: #ffffff;
        }
      }
      margin-top: 3rem;
      box-shadow: 0 0 10px rgba(153, 153, 153, 0.59);
      font-family: $Poppins;
      .p-name{
        padding: 1rem 0;

        @extend .center;
        h4{
          font-weight: bold;
          margin: 0;
        }
        p{
          margin: 0;
        }
      }
      .p-price{
        background: #f1f1f1;
        @extend .center;
        padding: 1rem 0;
        transition: .5s;
        h2{
          font-size: 40px;
          font-weight: bold;
          margin: 0;
        }
        p{
          margin: 0;
        }

      }
      .p-details{
        padding: 1rem .5rem 2rem;
        @extend .center;
        .details{
          text-align: left;
          i{
            margin-right: 5px;
          }
        }
        a.buy-package{
          margin: 0 auto;
          font-weight: bold;
          border-radius: 30px;
          transition: .5s;


        }

      }

    }
  }
}

#purchase_now{
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../img/purches.jpg") fixed top center no-repeat;
  background-size: cover;
  .pn-text{
    color: #ffffff;
    text-transform: capitalize;
    font-weight: bold;
    font-size: 36px;
    max-width: 600px;
    margin: 1rem auto;
    line-height: 1.4;
  }
}