.sidebar {
  .sidebar-widget {
    padding: 0 1rem;
    border: 1px solid #ddd;
    margin: 1rem 0;

    .sidebar-widget-title {
      h3 {
        font-weight: bold;

      }

      margin: 1.5rem 0 1rem;
    }

    .sidebar-widget-contents {}





    .search-widget {
      form.search {}
    }

    .popular-post-widget {
      .popular-post-widget-content {
        display: flex;
        margin-bottom: 2rem;

        .popular-post-widget-details {
          margin-left: 10px;

          .publish-date {
            margin-bottom: 0;
            color: $primary-color;
            font-size: 14px;

          }

          .post-title {
            h4 {
              font-size: 18px;
              font-weight: bold;
              color: #333;
            }
          }
        }

      }
    }



    .category-widget {
      .categories {
        a {
          color: #000;
          display: block;
          font-size: 18px;
          padding: .5rem 0;
          border-bottom: 1px solid #eee;

          i {
            margin-right: 5px;
          }

          &:hover {
            color: $primary-color;
          }
        }
      }
    }

    .archive-widget {
      ul.Archives {
        li {
          a {
            color: #000;
            display: block;
            font-size: 18px;
            padding: .5rem 0;
            border-bottom: 1px solid #eee;

            i {
              margin-right: 5px;
            }

            &:hover {
              color: $primary-color;
            }
          }
        }
      }
    }

    .contact-widget {
      .quick-contact-form {
        #name-c {}

        #email-c {}

        #massage-c {}

        .form-control {
          margin: .8rem 0;
        }

      }
    }

    .tag-widget {
      .tags {
        margin-bottom: 1rem;
        display: inline-block;
        a {
          color: #999;
          margin: .4rem;
          padding: .4rem .4rem;
          border: 1px solid #ddd;
          border-radius: 5px;
          transition: .5s;
          display: inline-block;

          &:hover {

            background: $primary-color;
            color: #fff;
          }
        }
      }
    }
  }
}